.filter__label {
  position: relative; }
  .filter__label--radio::before {
    position: absolute;
    content: "";
    top: -9px;
    left: -62px;
    width: 39px;
    height: 39px;
    border: 2px solid #444444;
    border-radius: 50%; }
    @media (min-width: 768px) {
      .filter__label--radio::before {
        width: 52px;
        height: 53px;
        top: -15px;
        left: -80px; } }
  .filter__label--checkbox::before {
    position: absolute;
    content: "";
    top: -12px;
    left: -62px;
    width: 40px;
    height: 39px;
    border: 2px solid #444444; }
    @media (min-width: 768px) {
      .filter__label--checkbox::before {
        top: -16px;
        left: -81px;
        width: 54px;
        height: 52px; } }
  .filter__label:hover::before {
    border-color: #63d1bb; }
  .filter__label:active::before {
    border-color: rgba(68, 68, 68, 0.3); }

.filter__input:focus + .filter__label--checkbox::before,
.filter__input:focus + .filter__label--radio::before {
  border-color: #63d1bb; }

.filter__input:checked + .filter__label--radio::before {
  box-shadow: inset 0 0 0 10px #ffffff, inset 0 0 0 19px #63d1bb; }
  @media (min-width: 768px) {
    .filter__input:checked + .filter__label--radio::before {
      box-shadow: inset 0 0 0 14px #ffffff, inset 0 0 0 25px #63d1bb; } }

.filter__input:checked + .filter__label--checkbox::before {
  background: url("../img/svg/icon-tick.svg") no-repeat center center;
  background-size: 24px; }
  @media (min-width: 768px) {
    .filter__input:checked + .filter__label--checkbox::before {
      background-size: 33px 22px; } }
