.social-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 240px;
  padding: 20px 0 0 0;
  list-style: none;
  box-shadow: 0 1px 0 rgba($gray, 0.1);

  @media (min-width: $tablet-width) {
    box-shadow: none;
  }
}

.social-list__item {
  margin-bottom: 20px;

  &:not(:nth-child(4n + 1)) {
    // Чтобы при добавлении новых элементов, они оказывались во второй строке по центру
    margin-left: 20px;
  }
}

.social-list__link {
  display: flex;
  width: 40px;
  height: 40px;

  &:hover .social-list__svg,
  &:focus .social-list__svg {
    fill: $medium-green;
  }

  &:active .social-list__svg {
    opacity: 0.3;
  }
}

.social-list__svg {
  margin: auto;
  pointer-events: none;
}
